import React from 'react';

export default class Filters extends React.Component {
    constructor(props) {
        super(props);

        this.callback = props.callback;
    }

    render() {
        return (
            <>
                <div className="cs-filters-wrapper">
                    <button onClick={() => this.callback('hi')}>Apply</button>
                </div>
            </>
        );
    }
}

